import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { NavController } from '@ionic/angular';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsBiometricButton, IOptionsPsContainerPanel, IOptionsPsLabel, IOptionsPsSelectToggle, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsComplexAlertController } from 'src/app/commonSRC/psTemplates/ps-template-alert-controller/ps-template-alert-controller.template.interfaces';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'alternative-login',
  templateUrl: './alternative-login.page.html',
  styleUrls: ['./alternative-login.page.scss'],
})
export class AlternativeLoginPage extends OmniBasePage implements OnInit {
  @Output() public AuthenticateBiometric: EventEmitter<any> = new EventEmitter<any>();

  public formGroup: FormGroup = new FormGroup({});
  public bioOptions: IOptionsPsBiometricButton = {
    group: this.formGroup,
    biometricOptions: {
      username: ""
    }

  };

  stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'alternative_login_key',
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: ['alt_login_step1'],
    group: this.formGroup,
    submitOptions: {
      // extraParams: {},
      submitServiceUrl: "",
      group: this.formGroup,
      preCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.updateBiometricOptionsSession(response));
          });
        },
        executionClass: this,
        params: [this]

      },

    },

  };
  panelOptions1Step1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'biometric_login_key',
    iconName: 'finger-print',
    expanded: true,

  };
  userName: any;
  userInfo: any;
  constructor(private navCtrl: NavController, public commonProv: PsCommonService, public loggerP: LoggerService, public omniPull: OmniPullService, private nav: PsNavigatorService) {
    super();
  }

  public biometricToggleOptions: IOptionsPsSelectToggle = {
    labelKey: 'enable_or_disable_biometrics_key',
    group: this.formGroup,
    fcName: 'biometricToggle',
    psClass: 'toggleColor',

  };
  biometricPnelHintOptions: IOptionsPsLabel = {
    labelKey: 'biometric_hint_key',
    position: "fixed"
  };

  ngOnInit() {
    super.init();
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, ['alternative_login_key_save_btn'], 0);
    this.commonProv.session.getStoredValueOf(CommonBussinessConstant.BIOMETRIC_LOGIN).then(result => {
      if (result) {
        this.biometricToggleOptions.group.controls[this.biometricToggleOptions.fcName].setValue(result);
      }
    })
    this.userInfo = this.commonProv.session.getValueOf(ConstantCommon.USERINFO)
    this.userName = this.userInfo[ConstantCommon.USER_NAME];
    this.bioOptions.biometricOptions.username = this.userName;

  };

  ionViewDidEnter() {
    super.ionViewDidEnter();
    if (this.commonProv.isNativeMobile()) {
      this.commonProv.isbiometricAvaliable().then((res) => {
        if (res == false) {
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, ['alternative_login_key_next_btn'], 1);
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.biometricToggleOptions.fcName], 1);
          this.biometricToggleOptions.labelKey = "device_do_not_support_biometrics_key"
        }
      });
    } else {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, ['alternative_login_key_next_btn'], 1);
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.biometricToggleOptions.fcName], 1);
      this.biometricToggleOptions.labelKey = "device_do_not_support_biometrics_key";
    }
  };

  onBiometricToggleClicked(event) {
    if (event && this.formGroup.controls[this.biometricToggleOptions.fcName].value) {
      this.checkAuthentication()
    }

  };
  checkAuthentication() {
    const checkAlertController: IOptionsPsComplexAlertController = {
      cssClass: 'cronto-code',
      header: 'biometric_successfully_validated_key',
      headerImageOptions: {
        imageName: '',
        imageBase64Url: ''
      },
      buttons: [
        {
          options: {
            labelKey: 'ok_key',
            group: null,
            psClass: 'ps-button-cancel'
          },
          buttonSize: 'col-12',
          handler: () => {
            CommonUtils.dismissAllModals();
          }
        }
      ]
    };
    this.commonProv.biometricAuth(this.bioOptions.biometricOptions).then(bioResult => {
      if (bioResult === ConstantCommon.BIOMETRIC_AUTH_SUCCESS) {
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, ['alternative_login_key_next_btn'], 0);
        // this.commonProv.session.append(CommonBussinessConstant.USER_AUTH_TO_ENABLE, true, false);
        checkAlertController.header = "biometric_successfully_validated_key";
        this.commonProv.presentPsAlert(checkAlertController, 'biometric-auth-success');

      } else if (bioResult === ConstantCommon.BIOMETRIC_NOT_ENROLLED) {
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, ['alternative_login_key_next_btn'], 1);
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.biometricToggleOptions.fcName], 1);
        this.biometricToggleOptions.labelKey = "register_at_least_one_biometric_id_under_your_phone_settings_in_order_to_proceed_key";

      } else if (bioResult === ConstantCommon.BIOMETRIC_HARDWARE_NOT_SUPPORTED) {
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, ['alternative_login_key_next_btn'], 1);
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.biometricToggleOptions.fcName], 1);
        this.biometricToggleOptions.labelKey = "device_do_not_support_biometrics_key";

      } else if (bioResult.code === ConstantCommon.FAIO_BIOMETRIC_DISMISSED) {
        this.formGroup.controls[this.biometricToggleOptions.fcName].setValue(false);

      } else {
        checkAlertController.header = "invalid_biometric_please_retry_key";
        checkAlertController.buttons = [
          {
            role: 'cancel',
            options: {
              labelKey: 'cancel_key',
              psClass: 'ps-drafts-report-cancel-button',
              group: null
            },
            handler: () => {
              this.formGroup.controls[this.biometricToggleOptions.fcName].setValue(false);
              this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, ['alternative_login_key_next_btn'], 1);

              CommonUtils.dismissAllModals();
            }
          }, {
            role: 'cancel',
            options: {
              labelKey: 'ok_key',
              iconName: '',
              psClass: 'ps-drafts-report-submit-button',
              group: null

            },
            handler: () => {

              this.checkAuthentication();
              CommonUtils.dismissAllModals();

            }
          }
        ];
        this.commonProv.presentPsAlert(checkAlertController, 'biometric-auth-success');

      }

    }).catch(error => {
      this.commonProv.logger.log(error);

    });
  }

  updateBiometricOptionsSession(response?: any) {
    const alertController: IOptionsPsComplexAlertController = {
      cssClass: 'cronto-code',
      header: 'biometrics_successfully_enabled_key',
      headerImageOptions: {
        imageName: '',
        imageBase64Url: ''
      },
      buttons: [
        {
          options: {
            labelKey: 'ok_key', //Modified by ghada, AZDB-2207 for seeting the right label to biometric submitting popup
            group: null,
            psClass: 'ps-button-cancel'
          },
          buttonSize: 'col-12',
          handler: () => {
            CommonUtils.dismissAllModals();
          }
        }
      ]
    };

    if (this.formGroup.controls[this.biometricToggleOptions.fcName].value) {
      //Edited by ghada, tp#1304017 for considering appId when saving remember me flag and user long / name in the session 
      //to fix the issue of having saved changes of one app not to reflect on the other app like remeber me flag and username appear on login when opening both retail and corporate apps in the same browser
      this.commonProv.session.append(CommonBussinessConstant.REMEMBER_ME + PsCommonSettings.APP_ID, true, true);
      this.commonProv.session.append(CommonBussinessConstant.BIOMETRIC_LOGIN, true, true)
      this.commonProv.session.append(CommonBussinessConstant.USER_NAME, this.userName, true);
      this.commonProv.session.append(CommonBussinessConstant.USER_LONG_NAME_KEY + PsCommonSettings.APP_ID, (this.userInfo?.customerInfoCO?.longName || this.userInfo?.customerInfoCO?.shortName), true);
      alertController.header = 'biometrics_successfully_enabled_key';
      this.commonProv.presentPsAlert(alertController, 'biometric-successfully-enabled');
      this.navCtrl.pop();
    }
    else {
      this.commonProv.session.append(CommonBussinessConstant.BIOMETRIC_LOGIN, false, true);
      alertController.header = "biometrics_successfully_disabled_key"

      this.commonProv.presentPsAlert(alertController, 'biometric-successfully-disabled');
      this.navCtrl.pop();

    }

  }


};


